<template class="bg-mirdc">
	<div class="form-container">
		<v-container>
      <v-layout wrap>
        <v-flex sm12 md6 offset-md3>
          <v-card elevation="4" light tag="section" class="transparent-card">
            <v-card-title>
              <v-layout align-center justify-center>
                <img width="250" src="../assets/sso-logo.svg" />
              </v-layout>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
              <p>Sign in with ID number and Password</p>
              <v-form v-model="valid" ref="form" lazy-validation>
                <p class="red--text text--darken-1" v-show="authError">
                  ID number or password is incorrect.
                </p>
                <v-text-field
                  outline
                  label="ID Number"
                  type="text"
                  v-model="idNumber"
                  :rules="idNumberRules"
                  required
                ></v-text-field>
                <v-text-field
                  outline
                  hide-details
                  label="Password"
                  type="password"
                  v-model="password"
                  required
                  :rules="passwordRules"
                ></v-text-field>
              </v-form>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions :class="{ 'pa-3': $vuetify.breakpoint.smAndUp }">
              <v-spacer></v-spacer>
              <v-btn :loading="loading" :disabled="loading" @click="submit" color="info" :large="$vuetify.breakpoint.smAndUp">
                <v-icon left>lock</v-icon>
                Login
              </v-btn>
              <v-spacer></v-spacer>
            </v-card-actions>
            <v-card-actions>
              <v-layout align-center justify-space-between>
            <p class="caption my-3">
              <a href="http://10.10.120.5/mirdcintranet/images/Policies/Administrative/POL-MIRDC-023.Rev0_Access-control-Guidelines.pdf" target="_blank">Policy Manual</a>
            </p>
            <p class="caption my-3">Powered by <a href="http://10.10.120.19:7171/">PMD-MIS</a></p>
          </v-layout>
            </v-card-actions>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

		<vue-snotify></vue-snotify>
	</div>
</template>

<script>
import { feathersClient } from '@/services';
var passwordValidator = require('password-validator');
	export default {
		data() {
			return {
				valid: true,
				idNumber: null,
				password: null,
				idNumberRules: [v => !!v || 'Enter your ID number'],
				passwordRules: [v => !!v || 'Enter your password'],
				color: '#1565C0',
				authError: false,
				icon: true,
				loading: false
			};
		},
		methods: {
      async submit() {
        // Check if ID number or password is empty
        if (!this.idNumber || !this.password) {
          this.$snotify.error('ID number and password are required');
          return;
        }

        let schema = new passwordValidator();

        // Add properties to it
        schema
          .is().min(10)                                    // Minimum length 10
          .is().max(100)                                  // Maximum length 100
          .has().uppercase()                              // Must have uppercase letters
          .has().lowercase()                              // Must have lowercase letters
          .has().digits(1)                                // Must have at least 1 digit
          .has().not().spaces()                           // Should not have spaces
          .has().symbols()                               // Must have symbols
          .is().not().oneOf(['Passw0rd', 'Password123', 'P@ssw0rd123' ]); // Blacklist these values

        const isPasswordValid = schema.validate(this.password);
        console.log("isPasswordValid", isPasswordValid);

        this.loading = true;
        try {
          await new Promise(resolve => setTimeout(resolve, 3000));

          const { userId, userData } = await this.attemptLogin();

          if (!isPasswordValid) {
            // Update the user record to set requirePasswordChange to true
            await feathersClient.service('users').patch(userId, {
              requirePasswordChange: true,
            });

            // Commit the mutation to set requirePasswordChange in the Vuex store
            this.$store.commit('account/SET_REQUIRE_PASSWORD_CHANGE', true);
          } else {
            // Update the user record to set requirePasswordChange to false
            await feathersClient.service('users').patch(userId, {
              requirePasswordChange: false,
            });

            // Commit the mutation to set requirePasswordChange in the Vuex store
            this.$store.commit('account/SET_REQUIRE_PASSWORD_CHANGE', false);
          }

          const { referer } = this.$route.query;
          if (referer) {
            location.replace(referer);
            return;
          }

          if (
            userData.roles &&
            userData.roles.sso &&
            userData.roles.sso.includes('admin')
          ) {
            if (!isPasswordValid) {
              // Update the user record to set requirePasswordChange to true
              await feathersClient.service('users').patch(userId, {
                requirePasswordChange: true,
              });

              // Commit the mutation to set requirePasswordChange in the Vuex store
              this.$store.commit('account/SET_REQUIRE_PASSWORD_CHANGE', true);
            } else {
              // Update the user record to set requirePasswordChange to false
              await feathersClient.service('users').patch(userId, {
                requirePasswordChange: false,
              });

              // Commit the mutation to set requirePasswordChange in the Vuex store
              this.$store.commit('account/SET_REQUIRE_PASSWORD_CHANGE', false);
            }

            this.$router.push({ name: 'Employees' });
          } else {
            this.$router.push({ name: 'Profile' });
          }
        } catch (error) {
          this.$snotify.error('Invalid credentials');
          console.error('ERROR!', error);
        } finally {
          this.loading = false;
        }
      },
      async attemptLogin() {
        const maxAttempts = 3; // Maximum number of login attempts
        let attempt = 1;

        while (attempt <= maxAttempts) {
          try {
            // Introduce a delay before each login attempt
            if (attempt > 1) {
              await new Promise(resolve => setTimeout(resolve, 4000)); // 4 seconds
            }

            // Perform the login
            return await this.$store.dispatch('account/LOGIN', {
              idNumber: this.idNumber,
              password: this.password
            });
          } catch (error) {
            // Handle any specific error, or let it propagate
            console.error(`Login attempt ${attempt} failed:`, error);
          }

          attempt++;
        }

        throw new Error(`Max login attempts (${maxAttempts}) reached.`);
      },
        }
      };
</script>

<style scoped>
	.form-container {
		margin-top: 10%;
	}
  .transparent-card {
    width: 100%;
    max-width: 500px;
    margin: 0 auto;
  }

</style>



