<template>
	<div>
		<v-container fluid>
			<v-layout row>
				<v-flex xs12 sm12 md12 lg12 xl9>
					<v-card>
						<v-card-title>
							<div class="headline white--text">Update Password</div>
							<v-spacer></v-spacer>
						</v-card-title>
						<v-divider></v-divider>
						<v-card-text>
							<v-form v-model="valid" ref="form" lazy-validation>
								<v-container fluid>
									<v-layout row>
										<v-flex xs4>
											<v-subheader>New Password</v-subheader>
										</v-flex>
										<v-flex xs12>
											<v-text-field :append-icon="pwordPrependIcon ? 'visibility' : 'visibility_off'"
											@click:append="pwordPrependIcon = !pwordPrependIcon"
											:type="pwordPrependIcon ? 'password' : 'text'" v-model="password.new" required :rules="passwordRules"></v-text-field>
										</v-flex>
									</v-layout>

									<v-layout row>
										<v-flex xs4>
											<v-subheader>Confirm Password</v-subheader>
										</v-flex>
										<v-flex xs12>
											<v-text-field :append-icon="confirmPrependIcon ? 'visibility' : 'visibility_off'"
											@click:append="confirmPrependIcon = !confirmPrependIcon"
											:type="confirmPrependIcon ? 'password' : 'text'" v-model="password.confirm" required :rules="passwordRules"></v-text-field>
										</v-flex>
									</v-layout>
								</v-container>
							</v-form>
						</v-card-text>
						<v-card-actions>
							<v-spacer></v-spacer>
							<v-btn color="primary" @click="save">Save</v-btn>
						</v-card-actions>
					</v-card>
				</v-flex>
			</v-layout>
		</v-container>

		<v-dialog v-model="loaderDialog" hide-overlay persistent width="300">
			<v-card color="primary" dark>
				<v-card-text>
					Please stand by
					<v-progress-linear
					indeterminate
					color="white"
					class="mb-0"
					></v-progress-linear>
				</v-card-text>
			</v-card>
		</v-dialog>

		<vue-snotify></vue-snotify>
	</div>
</template>



<script>
	import { mapGetters } from 'vuex';
	import {feathersClient} from '@/services';
  var passwordValidator = require('password-validator');
	const divisions = feathersClient.service('divisions');

	export default {
		data: () => ({
			valid: true,
			loading: false,
			loaderDialog: false,
			password: {
				new: null,
				confirm: null
			},
			passwordRules: [
        (v) => !!v || 'This field is required',
        (v) => {
          let schema = new passwordValidator();
          schema
            .is().min(10)
            .is().max(100)
            .has().uppercase()
            .has().lowercase()
            .has().digits(2)
            .has().not().spaces()
            .has().symbols()
            .is().not().oneOf(['Passw0rd', 'Password123', 'P@ssw0rd123']);

          return schema.validate(v) || 'Password must contain at least 10 characters, one uppercase letter, 1 numbers, and one special character';
        },
      ],
			pwordPrependIcon: true,
			confirmPrependIcon: true
		}),
    // mounted() {
    //   window.addEventListener('message', this.handleMessage);
    // },
		methods: {
			async save() {
        let schema = new passwordValidator();
        schema
          .is().min(10)
          .is().max(100)
          .has().uppercase()
          .has().lowercase()
          .has().digits(1)
          .has().not().spaces()
          .has().symbols()
          .is().not().oneOf(['Passw0rd', 'Password123', 'P@ssw0rd123']);

        const isPasswordValid = schema.validate(this.password.new && this.password.confirm) ;

        if (this.password.new !== this.password.confirm) {
          this.$snotify.error(`Passwords did not match`);
          return;
        }

        if (!this.$refs.form.validate()) {
          return;
        }
        this.loaderDialog = true;

        try {
          if (!isPasswordValid) {
          this.loaderDialog = false;
          this.$snotify.error('Password does not meet the complexity requirements.');
          return;
        }
        else {
          const res = await this.$store.dispatch('account/UPDATE_INFORMATION', {
            field: 'password',
            data: this.password.new
          });

          // Update requirePasswordChange to false
          await feathersClient.service('users').patch(res._id, {
            requirePasswordChange: false,
          });
          this.loaderDialog = false;
          this.$snotify.success('Password has been successfully updated');
        }
        } catch (error) {
          console.log(error);
          this.loaderDialog = false;
          this.$snotify.error('An error occurred');
        }
      },
      // handleMessage(event) {
      //   if (event.data && event.data.action === 'LOGIN') {
      //     const { success, currentUser, token } = event.data;

      //     // Use the received data as needed
      //     if (success) {
			// 	    this.$store.commit('account/SET_AUTHENTICATED', false);
			// 	    this.$store.commit('account/SET_EMPLOYEE_DATA', currentUser);
      //     }
      //   }
      // },
      // beforeDestroy() {
      //   window.removeEventListener('message', this.handleMessage);
      // }

		}
	}
</script>

<style scoped>
</style>



